/**
 * troika text related utilities.
 * Mainly for mocking convenience since both [preloadFont, sync] don't work in jest env.
 * Avoid import this module directly to reduce initial load
 */
import { Vector3 } from 'three';
import type { Text } from 'troika-three-text';
import { preloadFont as preloadFontTroika } from 'troika-three-text';

const DEFAULT_FONT_CHARACTERS = 'abcdefghijklmnopqrstuvwxyz';

/**
 * Load font file and kick off the font processing.
 * Promise gets resolved when preloadFont finishes.
 */
export function preloadFont(fontUrl?: string) {
	return new Promise<string>((res, rej) => {
		if (!fontUrl) {
			// We are defaulting to roboto as V5
			// Note: keep these as static imports or they break CSP policies
			import('../../fonts/roboto-regular.ttf').then(module => {
				preloadFontTroika(
					{
						font: module.default,
						characters: DEFAULT_FONT_CHARACTERS,
					},
					() => {
						res(module.default);
					},
				);
			});
		} else if (typeof fontUrl === 'string') {
			// if user supply a font file we can preload that font as well
			preloadFontTroika(
				{
					font: fontUrl,
					characters: DEFAULT_FONT_CHARACTERS,
				},
				() => {
					res(fontUrl);
				},
			);
		} else {
			rej(new Error(`bad fontUrl ${fontUrl}`));
		}
	});
}

const MIN_FONT_SIZE = 0.1;
const tempVec = new Vector3();
/**
 * Fit text to area with a given max width and height, scaling the font size down if necessary.
 */
export function fitTextToArea(text: Text, maxWidth: number, maxHeight: number, scaleStep: number) {
	if (maxWidth <= 0 || maxHeight <= 0) {
		return Promise.reject(`incoorect dimension [${maxWidth}, ${maxHeight}]`);
	}

	return new Promise<{ size: [width: number, height: number] }>((res, rej) => {
		text.sync(syncCallback);

		function syncCallback() {
			if (!text.geometry.boundingBox) return;

			const { x: width, y: height } = text.geometry.boundingBox.getSize(tempVec);

			if (height > maxHeight || width > maxWidth) {
				if (text.fontSize === MIN_FONT_SIZE) {
					return rej(
						`cannot find suitable font size for ${text.text}. MaxSize: [${maxWidth}, ${maxHeight}],  Current size: [${width}, ${height}]. min font size${MIN_FONT_SIZE}.`,
					);
				}
				text.fontSize = Math.max(MIN_FONT_SIZE, text.fontSize - scaleStep);
				text.sync(syncCallback);
			} else {
				return res({ size: [width, height] });
			}
		}
	});
}

export function syncText(text: Text) {
	return new Promise<void>(resolve => {
		text.sync(() => {
			resolve();
		});
	});
}

/**
 * Loads the default font without additional preprocessing
 */
export async function loadDefaultFont(): Promise<string> {
	// Note: keep these as static imports or they break CSP policies
	const module = await import('../../fonts/roboto-regular.ttf');

	return module.default;
}
