import { MathUtils, Matrix4, type Mesh, Vector3 } from 'three';
import { DEFAULT_TEXT_STYLE, type Text3DStyleComponent } from '../../components/styles';
import { BatchedText, Text, configureTextBuilder } from 'troika-three-text';
import type { RendererCore } from '../..';
import type { Text3DComponent, Text3DProperties } from '../../components/text3d';
import { type FloorTextCommonProperties } from '@mappedin/mvf-v2';
import { normalizeAngle } from '../../utils';
import { syncText, preloadFont as preloadFontFromModule, fitTextToArea, loadDefaultFont } from './utils';

export function configureTroikaTextBuilder({ useWorker = true }: { useWorker: boolean }) {
	configureTextBuilder({
		useWorker,
	});
}

const tempMatrix = new Matrix4();
const tempVec = new Vector3();
const Z_AXIS_BASISVECTOR = new Vector3(0, 0, 1);

export async function createTroikaTextPoint(
	entityId: string | number,
	text3DComponent: Text3DComponent,
	styleComponent: Text3DStyleComponent,
	convertTo3DMapPosition: RendererCore['convertTo3DMapPosition'],
) {
	const properties = text3DComponent.feature.properties as FloorTextCommonProperties & { verticalOffset: number };

	const text = new Text();
	text3DComponent.textMesh = text;
	text.text = properties.text;

	// Make sure text rendering is same as V5: center aligned.
	text.anchorX = 'center';
	text.anchorY = 'middle';
	text.textAlign = properties.align ?? 'center';
	text.color = properties.color ?? 'black';
	text.fontSize = properties.fontSize || 20;

	// font handling
	const font = styleComponent.font ?? DEFAULT_TEXT_STYLE.font;
	if (font) {
		// let troika take over the font processing and caching
		text.font = font;
	} else if (fontPreloadLoadPromise) {
		text.font = await fontPreloadLoadPromise;
	} else {
		text.font = await loadDefaultFont();
	}

	// Mark: handle styles
	text.outlineOpacity = styleComponent.outlineOpacity ?? DEFAULT_TEXT_STYLE.outlineOpacity;
	text.outlineColor = styleComponent.outlineColor ?? DEFAULT_TEXT_STYLE.outlineColor;
	text.outlineOffsetY = styleComponent.outlineOffsetY ?? DEFAULT_TEXT_STYLE.outlineOffsetY;
	text.outlineOffsetX = styleComponent.outlineOffsetX ?? DEFAULT_TEXT_STYLE.outlineOffsetX;
	text.outlineWidth = styleComponent.outlineWidth ?? DEFAULT_TEXT_STYLE.outlineWidth;
	text.outlineBlur = styleComponent.outlineBlur ?? DEFAULT_TEXT_STYLE.outlineBlur;
	text.strokeWidth = styleComponent.strokeWidth ?? DEFAULT_TEXT_STYLE.strokeWidth;
	text.strokeColor = styleComponent.strokeColor ?? DEFAULT_TEXT_STYLE.strokeColor;
	text.strokeOpacity = styleComponent.strokeOpacity ?? DEFAULT_TEXT_STYLE.strokeOpacity;

	const position = convertTo3DMapPosition(text3DComponent.feature.geometry.coordinates);

	text.position.copy(position);
	text.position.z = properties.verticalOffset;
	text.rotation.z = MathUtils.degToRad(-properties.rotation);

	text.userData = { entityId: entityId.toString(), rotationZ: -properties.rotation };

	return { text };
}

export async function createTroikaTextArea(
	entityId: string | number,
	text3DComponent: Text3DComponent,
	styleComponent: Text3DStyleComponent,
	convertTo3DMapPosition: RendererCore['convertTo3DMapPosition'],
) {
	const properties = text3DComponent.feature.properties as Text3DProperties;
	const { textArea } = properties;
	if (!textArea) throw new Error('textArea is required');

	const marginOption = styleComponent.margin ?? DEFAULT_TEXT_STYLE.margin;
	const [topM, rightM, bottomM, leftM] = Array.isArray(marginOption)
		? marginOption
		: [marginOption, marginOption, marginOption, marginOption];
	const text = new Text();
	text.text = properties.content;

	const areaMaxHeight = textArea.maxHeight - topM - bottomM;
	const areaMaxWidth = textArea.maxWidth - leftM - rightM;
	text.fontSize = Math.min(areaMaxHeight, styleComponent.fontSize ?? DEFAULT_TEXT_STYLE.fontSize); // TODO handle default font size
	text.color = styleComponent.color ?? DEFAULT_TEXT_STYLE.color;
	text.maxWidth = styleComponent.maxWidth ?? areaMaxWidth;

	// Make sure text rendering is same as V5: center aligned.
	text.anchorX = 'center';
	text.anchorY = 'middle';
	text.textAlign = 'center';

	// font handling
	const font = styleComponent.font ?? DEFAULT_TEXT_STYLE.font;
	if (font) {
		// let troika take over the font processing and caching
		text.font = font;
	} else if (fontPreloadLoadPromise) {
		// if font is getting preloaded, we use the preloaded font
		text.font = await fontPreloadLoadPromise;
	} else {
		// font is not preloaded and undefined, import the default (Roboto)
		text.font = await loadDefaultFont();
	}

	// Mark: handle styles
	text.outlineOpacity = styleComponent.outlineOpacity ?? DEFAULT_TEXT_STYLE.outlineOpacity;
	text.outlineColor = styleComponent.outlineColor ?? DEFAULT_TEXT_STYLE.outlineColor;
	text.outlineOffsetY = styleComponent.outlineOffsetY ?? DEFAULT_TEXT_STYLE.outlineOffsetY;
	text.outlineOffsetX = styleComponent.outlineOffsetX ?? DEFAULT_TEXT_STYLE.outlineOffsetX;
	text.outlineWidth = styleComponent.outlineWidth ?? DEFAULT_TEXT_STYLE.outlineWidth;
	text.outlineBlur = styleComponent.outlineBlur ?? DEFAULT_TEXT_STYLE.outlineBlur;
	text.strokeWidth = styleComponent.strokeWidth ?? DEFAULT_TEXT_STYLE.strokeWidth;
	text.strokeColor = styleComponent.strokeColor ?? DEFAULT_TEXT_STYLE.strokeColor;
	text.strokeOpacity = styleComponent.strokeOpacity ?? DEFAULT_TEXT_STYLE.strokeOpacity;

	const pos = convertTo3DMapPosition(textArea.position, tempVec);
	text.position.copy(pos);
	const rotationZ = normalizeAngle(Math.PI / 2 - MathUtils.degToRad(textArea.rotation)); // textArea rotation 90 degrees points to true north, and rotate off that
	text.rotation.z = rotationZ;
	text.userData = { entityId: entityId.toString(), rotationZ };

	let textAreaMesh: undefined | Mesh;

	let size: [width: number, height: number] | undefined;
	try {
		size = (await fitTextToArea(text, areaMaxWidth, areaMaxHeight, text.fontSize * 0.2)).size;
	} catch (e) {
		text.dispose?.();
		throw new Error(`Cannot fit text to area: entityId: ${text3DComponent.polygonEntityId} ${e}`);
	}

	if (!size) return;

	// handling margin: offset the text along the vector that's rotated correctly
	tempMatrix.identity();
	tempVec.set(0, 0, 1);
	tempMatrix.makeRotationAxis(Z_AXIS_BASISVECTOR, rotationZ);
	tempVec.set(textArea.align === 'near' ? leftM + size[0] / 2 : textArea.maxWidth / 2, 0, 0);
	tempVec.applyMatrix4(tempMatrix);

	text.position.x -= tempVec.x;
	text.position.y -= tempVec.y;
	text3DComponent.textMesh = text;

	if (textAreaMesh) {
		tempVec.set(textArea.maxWidth / 2, 0, 0);
		tempVec.applyMatrix4(tempMatrix);
		textAreaMesh.position.x -= tempVec.x;
		textAreaMesh.position.y -= tempVec.y;
	}

	return { text };
}

export { BatchedText, Text, syncText };

/**
 * Promise caches the base64 font string. Then shared across all needed
 */
export let fontPreloadLoadPromise: Promise<string> | undefined;

/**
 * Preloads a font from the given URL. Enable developer to preload font way before scene is ready
 *
 * @param  fontUrl - The URL of the font to preload. When undefined, default font (Roboto) is loaded
 * @returns A promise that resolves when the font is successfully preloaded, or rejects with an error if the font cannot be loaded.
 */
export function preloadFont(fontUrl?: string) {
	fontPreloadLoadPromise = preloadFontFromModule(fontUrl);

	return fontPreloadLoadPromise;
}

/**
 * typeof module is forbiten in typescript. see: [@typescript-eslint/consistent-type-imports]
 */
export type ModuleType = {
	createTroikaTextPoint: typeof createTroikaTextPoint;
	createTroikaTextArea: typeof createTroikaTextArea;
	configureTroikaTextBuilder: typeof configureTroikaTextBuilder;
	preloadFont: typeof preloadFont;
	syncText: typeof syncText;
	BatchedText: typeof BatchedText;
};
